.scalerContainer {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.scaleAddButton {
    position: absolute;
    bottom: 75px;
    right: 75px;
}

.scaler {
    -webkit-box-shadow: 2px 4px 8px 0 rgba(0, 0, 0, 0.16);
            box-shadow: 2px 4px 8px 0 rgba(0, 0, 0, 0.16);
    border-radius: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    margin: 10px;
    background-color: white;
    margin: 25px;
    overflow-y: scroll;
    -ms-flex-positive: 1;
        flex-grow: 1;
    position: relative;
}

.scaler::-webkit-scrollbar-track
{
    margin: 17px;
	border-radius: 15px;
    background-color:rgba(0, 0, 0, 0.2);
}

.scaler::-webkit-scrollbar
{
	width: 5px;
    background-color:transparent;
}

.scaler::-webkit-scrollbar-thumb
{
	border-radius: 15px;
	background-color: grey;
}

.scaler > * {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    margin-top: 25px;
}

.scaler > * + *:last-child {
    margin-bottom: 25px;
}

@media (max-width: 1200px) {
    .scalerContainer {
        -ms-flex-order: 1;
            order: 1;
        min-height: 100px;
        width: 100%;
        overflow-x: auto;
    }
