html, body, #root {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, right bottom, from(#003c8f), to(#0069c0));
  background: -webkit-linear-gradient(top left, #003c8f, #0069c0);
  background: -o-linear-gradient(top left, #003c8f, #0069c0);
  background: linear-gradient(to bottom right, #003c8f, #0069c0);
}



