.selector {
    height: 100%;
    width: 250px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow-y: scroll;
    overflow-x: none;
    background-color: #F6F6F6;
    -webkit-transition: left 1s;
    -o-transition: left 1s;
    transition: left 1s;
    -webkit-box-shadow: 2px 4px 8px 0 rgba(0, 0, 0, 0.16);
            box-shadow: 2px 4px 8px 0 rgba(0, 0, 0, 0.16);
    z-index: 999;
}
.selector > * {
    margin-top: 25px;
}
.selector > *:last-child {
    margin-bottom: 25px;
}

.selectorAddButton {
    min-height: 50px;
    height: 90px;
    width: 90px;
    border-radius: 10px;
    margin-top: 25px;
    border: 2px dashed #004ead;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    cursor: pointer;
    color: #004ead;
    font-size: 30px;
    background-color: #F6F6F6;
}

.selectorAddButton:disabled {
    color: grey;
    border-color: grey;
}

.emptySelector {
    text-align: center;
}

@media (max-width: 1200px) {
    .selector {
        -ms-flex-direction: row;
            flex-direction: row;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        -ms-flex-item-align: center;
            align-self: center;
        -ms-flex-order: 0;
            order: 0;
        -ms-flex-negative: 0;
            flex-shrink: 0;
        width: 100%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        overflow-y: none;
    }
    .selector > * {
        margin: 25px;
        margin-right: 0;
    }
    .selector > *:last-child {
        margin-right: 25px;
    }
}


